import { MdOutlineShoppingCartCheckout } from "react-icons/md";
import { TbCoinRupee } from "react-icons/tb";
import { FaRupeeSign } from "react-icons/fa6";
import { HiOutlineReceiptPercent } from "react-icons/hi2";

const features = {
  "fast refunds": <TbCoinRupee />,
  "one-click checkout": <MdOutlineShoppingCartCheckout />,
  "exclusive offers": <HiOutlineReceiptPercent />,
  "balanced funds": <FaRupeeSign />,
};

const Features = () => {
  return (
    <div className="bg-lightGray p-6 rounded-lg">
      <ul className="grid grid-cols-4 max-md:grid-cols-2 gap-6 max-lg:gap-4">
        {Object.keys(features).map((feature) => (
          <li
            key={feature}
            className="flex flex-col items-center text-center gap-2"
          >
            <p className="lg:text-3xl text-2xl text-primeRed">
              {features[feature]}
            </p>
            <p className="capitalize font-medium text-sm text-dark">
              {feature}
            </p>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Features;
