import { useState } from "react";
// import { FaPlus } from "react-icons/fa6";
import useForm from "../../hooks/useForm";
import { useWallet } from "../../contexts/walletContext";
import usePayment from "../../hooks/usePayment";
import { useToast } from "../../contexts/toastContext";

const Balance = () => {
  const [isAdding, setIsAdding] = useState(false);
  const toast = useToast();
  const { balance, addBalance } = useWallet();
  const { processPayment, loading: paymentLoading } = usePayment();

  const addBalanceOnSuccess = async (balance) => {
    await addBalance(balance, "Wallet Top-Up");
    clearForm();
    setIsAdding(false);
  };

  const onSubmitHandler = async (data) => {
    const { balance } = data;

    // Client side validations
    if (!balance) {
      toast.error("Please enter an amount");
      return;
    }

    await processPayment(
      {
        amount: balance,
        currency: "INR",
        paymentMethod: "Razorpay",
        orderId: Math.floor(Math.random() * 1000000 + 1),
      },
      () => addBalanceOnSuccess(balance)
    );
  };

  const { formData, handleChange, handleSubmit, clearForm } = useForm(
    { balance: "" },
    onSubmitHandler
  );

  return (
    <div className="p-6 bg-white rounded-lg shadow-md space-y-6">
      <h4 className="text-xl font-semibold text-gray-800">Wallet Balance</h4>
      <p className="text-success font-bold text-4xl">₹ {balance}</p>
      {isAdding ? (
        <form
          onSubmit={handleSubmit}
          className="flex flex-wrap items-center gap-4"
        >
          <input
            type="number"
            name="balance"
            min={0}
            value={formData.balance}
            onChange={handleChange}
            placeholder="Enter amount"
            className="px-4 py-2 rounded-md text-base border border-gray-300 focus:border-primeRed focus:outline-none focus:ring-1 focus:ring-primeRed transition duration-150 ease-in-out"
          />
          <button
            disabled={paymentLoading}
            className="px-5 py-2 bg-primeRed text-white rounded-md text-base font-medium hover:bg-primeRed-dark transition duration-150 ease-in-out"
          >
            Add
          </button>
          <button
            type="button"
            onClick={() => setIsAdding(false)}
            className="px-5 py-2 border border-gray-300 text-gray-700 rounded-md text-base font-medium hover:bg-gray-100 transition duration-150 ease-in-out"
          >
            Cancel
          </button>
        </form>
      ) : // <button
      //   onClick={() => setIsAdding(true)}
      //   className="flex items-center gap-2 px-5 py-2 bg-primeRed text-white rounded-md text-base font-medium hover:bg-primeRed-dark transition duration-150 ease-in-out"
      // >
      //   <FaPlus />
      //   <p>Add Balance</p>
      // </button>
      null}
    </div>
  );
};

export default Balance;
