import { useMemo } from "react";
import Pagination from "../../components/Pagination";
import { useWallet } from "../../contexts/walletContext";
import { useSearchParamsState } from "../../hooks/useSearchParamsState";
import { formatDate } from "../../utils/dateUtils";

const Transaction = () => {
  const { transactions } = useWallet();
  const [page, setPage] = useSearchParamsState("page", 1, true);
  const limit = 5;

  const currentTableData = useMemo(() => {
    const firstPageIndex = (page - 1) * limit;
    const lastPageIndex = firstPageIndex + limit;
    return transactions.slice(firstPageIndex, lastPageIndex);
  }, [page, transactions, limit]);

  return (
    <div className="space-y-4">
      <h2 className="text-xl font-medium text-gray-700">Recent Transactions</h2>

      {transactions.length === 0 ? (
        <div className="w-full">
          <h2 className="text-center text-sm text-primary">
            No Transactions made yet
          </h2>
        </div>
      ) : (
        <>
          <div className="overflow-x-auto">
            <table className="min-w-full bg-gray-50 border border-gray-200 rounded-lg shadow-md">
              <thead>
                <tr className="bg-gray-100 text-gray-600 text-sm leading-normal">
                  <th className="py-3 px-6 text-left">Date</th>
                  <th className="py-3 px-6 text-left">Description</th>
                  <th className="py-3 px-6">Amount</th>
                  <th className="py-3 px-6">Status</th>
                </tr>
              </thead>
              <tbody className="text-gray-700 text-sm">
                {currentTableData.map((transaction, index) => (
                  <tr
                    key={index}
                    className="border-b border-gray-200 hover:bg-gray-100"
                  >
                    <td className="py-3 px-6 whitespace-nowrap">
                      {formatDate(new Date(transaction.date))}
                    </td>
                    <td className="py-3 px-6">{transaction.description}</td>
                    <td
                      className={`py-3 px-6 font-medium text-center ${
                        transaction.amount <= 0
                          ? "text-red-500"
                          : "text-green-500"
                      }`}
                    >
                      {transaction.amount}
                    </td>
                    <td
                      className={`py-3 px-6 text-center ${
                        transaction.type === "credit"
                          ? "text-green-600"
                          : "text-yellow-600"
                      }`}
                    >
                      {transaction.type}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>

          <Pagination
            currentPage={parseInt(page, 10)}
            totalCount={transactions.length}
            pageSize={limit}
            onPageChange={(page) => setPage(page.toString())}
          />
        </>
      )}
    </div>
  );
};

export default Transaction;
